import * as sdk from './request';
import wpo from '@ali/retcodelog';

let dingSDK = null;

function init(){
    wpo.setConfig({
        //抽样分母，1代表100%上报，10代表10%上报，100代表1%上报
        sample: 1,
        // spmId必须设置,如果没写默认值,会自动抓页面的spm
        spmId: 'DINGDING_ICEPY'
    });
    wpo.log('DING WEB INTERFACE SDK START',1);
    return sdk;
}

if(!dingSDK){
    dingSDK = init();
}

export default dingSDK;


